<template lang="pug">
  .wrapper-cart

    #cart-button
      button.btn.btn-success(
        type='button'
        data-toggle='modal'
        data-target='#modal-cart'
      )
        i.material-icons shopping_cart
        span ${{ subtotal }}

    // Modal
    #modal-cart.modal.fade(
      role='dialog'
      aria-labelledby='modal-cart-label'
      aria-hidden='true'
    )
      .modal-dialog(
        role='document'
      )
        .modal-content
          .modal-header
            h5.modal-title#modal-cart-label Your Cart
            button.close(
              type='button'
              data-dismiss='modal'
              aria-label='Close'
            )
              span(
                aria-hidden='true'
              ) &times;
          .modal-body
            .container(
              v-if='Object.entries(orderData).length !== 0'
            )
              .row
                .col(
                  v-for='(value, key) in orderData'
                )
                  SaleItem(
                    :name='key'
                    :key='key'
                    :magnet_count='value["magnet"]'
                    :button_count='value["button"]'
                    @update='update'
                  )
            .container(
              v-else
            )
              p Your cart is empty!
</template>

<script>
  import SaleItem from '@/components/SaleItem'
  import pricing from '@/assets/pricing.json'

  export default {
    data() {
      return {}
    },
    props: [
      'orderData'
    ],
    components: {
      'SaleItem': SaleItem
    },
    methods: {
      update(data) {
        this.$emit('update', data)
      },
      applyDeal(nitems, deal) {
        const quotient = Math.floor(nitems / deal.amount)
        const deal_price = quotient * deal.price
        return {
          price: deal_price,
          nitems: (nitems - (quotient * deal.amount))
        }
      }
    },
    computed: {
      subtotal() {
        var price = 0
        var nitems = 0
        for (var key in this.orderData) {
          for (var key2 in this.orderData[key]) {
            nitems += this.orderData[key][key2]
          }
        }
        for (var i = 0; i < pricing.deals.length; i++) {
          var deal = pricing.deals[i]
          var result = this.applyDeal(nitems, deal)
          price += result.price
          nitems = result.nitems
        }
        price += (nitems * pricing.base_price)
        return price
      }
    }
  }
</script>

<style lang="sass" scoped>

  #cart-button
    position: fixed
    top: 1rem
    right: 1rem
    z-index: 10
    display: table
    i, span
      font-size: 2rem
      color: white
      background-color: transparent
      margin: auto 0
      line-height: 2rem
      display: table-cell
      vertical-align: middle,
    span
      font-size: 1rem

</style>