import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import HomePage from './pages/Home'
import Cart from './components/Cart'

Vue.config.productionTip = false

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomePage
  },
  // {
  //   path: '/test',
  //   name: 'test',
  //   component: Cart
  // }
]

const router = new VueRouter({
  routes
})


new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
