<template lang="pug">
  .wrapper-sale-item
    script(
      v-html='jsonld'
      type='application/ld+json'
    )
    .card
      img.card-img-top(
        :src='getItemImageSrc(name)'
      )
      .card-body
        h5 {{ title || name }}
        #amounts(
          v-show='show_amounts'
        )
          // Magnets
          p magnets: {{ local_magnet_count }}
          i.material-icons.add.text-primary(
            @click='add("magnet")'
          ) add_circle_outline
          i.material-icons.remove.text-warning(
            @click='remove("magnet")'
          ) remove_circle_outline

          // Buttons
          p buttons: {{ local_button_count }}
          i.material-icons.add.text-primary(
            @click='add("button")'
          ) add_circle_outline
          i.material-icons.remove.text-warning(
            @click='remove("button")'
          ) remove_circle_outline
</template>

<script>
export default {
  data() {
    return {
      local_magnet_count: this.magnet_count,
      local_button_count: this.button_count,
      jsonld: {
        '@context': 'https://schema.org',
        '@type': 'Recipe',
        'name': this.name,
        'image': this.getItemImageSrc(this.name),
        'description': `League of Legends item "${this.name}"` 
      }
    }
  },
  props: {
    'name': String,
    'drawn': Boolean,
    'title': String,
    'show_amounts': {
      type: Boolean,
      default: true
    },
    button_count: {
      type: Number,
      default: 0
    },
    magnet_count: {
      type: Number,
      default: 0
    }
  },
  methods: {
    update(data) {
      this.$emit('update', data)
    },
    processName(name) {
      return name.replace(/[\'\(\)]/g,"").replace(/\s+/g,'_').toLowerCase()
    },
    getItemImageSrc(item_name) {
      var processed_name = this.processName(item_name)
      if (this.drawn) {
        processed_name = processed_name + '_drawn'
      }
      try {
        var result = require(
          `../assets/images/${processed_name}.jpg`
        )
        return result
      } catch(err) {
        console.error(err)
        return ''
      }
    },
    add(item_type) {
      if (item_type == 'magnet') {
        this.local_magnet_count ++
        this.update({item_type: item_type, count: this.local_magnet_count, name: this.name})
      } else {
        this.local_button_count ++
        this.update({item_type: item_type, count: this.local_button_count, name: this.name})
      }
    },
    remove(item_type) {
      if (item_type == 'magnet') {
        if (this.local_magnet_count > 0) {
          this.local_magnet_count --
          this.update({item_type: item_type, count: this.local_magnet_count, name: this.name})
        }
      } else {
        if (this.local_button_count > 0) {
          this.local_button_count --
          this.update({item_type: item_type, count: this.local_button_count, name: this.name})
        }
      }
    }
  },
  watch: {
    magnet_count(new_val, old_val) {
      this.local_magnet_count = new_val
    },
    button_count(new_val, old_val) {
      this.local_button_count = new_val
    }
  }
}
</script>

<style lang="sass" scoped>

  .card
    max-width: 10rem
    margin-bottom: 2rem

  p
    margin-bottom: 0
    margin-top: 1rem

  i
    cursor: pointer
    font-size: 2rem

</style>