<template lang="pug">
  .homepage-wrapper.container
    // Cart
    .section#cart
      Cart(
        :orderData='orderData'
        @update='updateOrder'
      )

    // Jumbotron
    hr
    .section#jumbo
      .jumbotron
        h1.display-4 Welcome to Pictobox!
        p.lead This is our home page where you can browse and purchase a variety of hand-drawn art
        hr.my-4
        p Scroll down to see our store!

    // Information
    hr
    .section#info
      h1 League of Legends Buttons and Magnets
      p The art on our buttons and magnets have been re-drawn by hand on a larger scale. Dimensions are 2 x 2 inches
      .container.md
        .row.justify-content-center.no-gutters
          .col(
            align='center'
          )
            SaleItem.saleitem(
              name='phantom_dancer'
              title='Original Artwork'
              :show_amounts='false'
            )
          .col(
            align='center'
          )
            i.material-icons.icon_huge trending_flat
          .col(
            align='center'
          )
            SaleItem.saleitem(
              name='phantom_dancer',
              :drawn='true'
              title='Re-drawn Artwork'
              :show_amounts='false'
            )
      h3 Real World Image
      .container
        .row
          .col
            figure.figure.sample
              img.figure-img.img-fluid.rounded(
                :src='require("../assets/images/magnet_sample.jpg")'
              )
              figcaption.figure-caption Actual photo of magnet

    // Store and pricing
    hr
    .section#store
      // Pricing
      h1 Pricing
      ul.list-group#pricing-list
        li.list-group-item 1 for $4 USD
        li.list-group-item.list-group-item-warning 3 for $10 USD
        li.list-group-item.list-group-item-success 6 for $20 USD
      .spacer
      .alert.alert-primary Free shipping on orders of $50 or more!
      .spacer

      // Store
      h1 Store
      p To protect our content, original art is shown as a placeholder. Actual buttons and magnets will be like seen above.
      SearchBar(
        v-model='search_query'
      )
      .row
        .col(
          align='center'
          v-for='item in filteredItems'
        )
          SaleItem.saleitem(
            :name='item.name'
            :button_count='getCount(item, "button")'
            :magnet_count='getCount(item, "magnet")'
            @update='updateOrder'
          )

    hr
    .section#footer
      h2
      .container
        .row
          .col(
            align='center'
          )
            OrderForm(
              :orderData='orderData'
            )
</template>

<script>
import SaleItem from '@/components/SaleItem'
import SearchBar from '@/components/SearchBar'
import OrderForm from '@/components/OrderForm'
import Cart from '@/components/Cart'

import SaleItemData from '@/assets/inventory.json'

export default {
  data() {
    return {
      items: SaleItemData,
      search_query: '',
      orderData: {}
    }
  },
  components: {
    'SaleItem': SaleItem,
    'SearchBar': SearchBar,
    'OrderForm': OrderForm,
    'Cart': Cart
  },
  computed: {
    filteredItems() {
      return this.items.filter(item => {
        return item.name.toLowerCase().includes(this.search_query.toLowerCase())
      })
    },
  },
  methods: {
    filterObject(obj, predicate) {
      var result = {}, key
      for (key in obj) {
        if (obj.hasOwnProperty(key) && predicate(obj[key])) {
          result[key] = obj[key]
        }
      }
      return result
    },
    updateOrder(data) {
      if (! this.orderData.hasOwnProperty(data['name'])) {
        this.$set(
          this.orderData,
          data['name'],
          {[data['item_type']]: data['count']}
        )
      } else {
        this.$set(
          this.orderData[data['name']],
          data['item_type'],
          data['count']
        )
      }
      if (
        (
          this.orderData[data['name']]['button'] == 0 && (
            this.orderData[data['name']]['magnet'] == 0 ||
            ! this.orderData[data['name']].hasOwnProperty('magnet')
          )
        ) || (
          this.orderData[data['name']]['magnet'] == 0 && (
            this.orderData[data['name']]['button'] == 0 ||
            ! this.orderData[data['name']].hasOwnProperty('button')
          )
        )
      ) {
        this.$delete(this.orderData, data['name'])
      }
    },
    getCount(item, item_type) {
      if (this.orderData.hasOwnProperty(item['name'])) {
        return this.orderData[item['name']][item_type]
      } else {
        return 0
      }
    }
  }
}
</script>

